import React from "react";
import { useSelector } from "react-redux";
import FoliaCard from "./FoliaCard";
import { MDBIcon, MDBRow, MDBCol } from "mdbreact";

import Slider from "react-animated-slider";
import "react-animated-slider/build/horizontal.css";

export default function FoliaCarousel(props) {
  let produtos = useSelector(state => state.products.produtos);
  produtos = produtos.filter(produto => {
    return produto.type === "grouped";
  });
  produtos = produtos.filter(function(produto) {
    return produto.categories[0].id === props.categoriaID;
  });
  let NomeCategoria = "";
  if (produtos.length > 0) {
    NomeCategoria = produtos[0].categories[0].name;
  }

  let sliderRef;

  function slidePrevious() {
    if (sliderRef) {
      sliderRef.previous();
    }
  }

  function slideNext() {
    if (sliderRef) {
      sliderRef.next();
    }
  }

  return (
    <section className="text-center my-3 w-100">
      <MDBRow
        className="h1-responsive font-weight-bold my-2"
        style={{ color: "#4A4A4A!important" }}
      >
        <MDBCol size="8">
          <h2 className="text-left ">{NomeCategoria}</h2>
        </MDBCol>
        <MDBCol size="4">
          <h2 className="text-right">
            <MDBIcon
              className="mr-4"
              icon="angle-left"
              onClick={slidePrevious}
            />
            <MDBIcon icon="angle-right" onClick={slideNext} />
          </h2>
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <MDBCol>
          <Slider
            ref={ref => (sliderRef = ref)}
            previousButton={null}
            nextButton={null}
            touchDisabled={false}
          >
            {produtos.map(item => (
              <div key={item.id}>
                <FoliaCard dados={item} />
              </div>
            ))}
          </Slider>
        </MDBCol>
      </MDBRow>
    </section>
  );
}
